@import "../../../base";

.landing-blog-container {
  margin: 30px 0 50px 0;
  padding: 0 15px;

  @media screen and (max-width: 991px) {
    max-width: 600px;
    margin: auto;
  }

  .blog-carousel-container {
    margin: 10px auto 50px auto;
    max-width: 480px;
  
    .slick-list {
      border-radius: 20px;
      overflow: hidden;
    }
    .blog-img {
      width: 480px;
      height: 320px;
      object-fit: cover;

      @media screen and (max-width: 767px) {
        height: 320px;
      }

      @media screen and (max-width: 575px) {
        height: 160px;
      }
    }
  }

  .blog-info-container {
    padding-left: 50px;
    @media screen and (max-width: 575px) {
      min-height: 300px;
    }

    @media screen and (max-width: 991px) {
      min-height: 250px;
      max-width: 600px;
      margin: 0 auto;
      padding-left: 0;
    }

    h3 {
      font-weight: $font-weight-bold;
      font-size: $font-size-small + $font-size-tiny;
      color: $color-input-text;

      @media screen and (max-width: 991px) {
        font-size: $font-size-regular;
      }
    }

    .date {
      font-size: $font-size-tiny;
      color: $color-other-desc;
    }

    .info {
      font-weight: $font-weight-normal;
      font-size: $font-size-smallest;
      color: $color-input-form-landing-desc2;

      @media screen and (max-width: 991px) {
        font-size: $font-size-tiny;
      }
    }

    .read-more {
      font-size: $font-size-smallest;
    }
  }
}
